import { Component, OnInit } from "@angular/core";

import { TeacherService } from "../services/teacher.service";
import { ActivatedRoute, Router } from "@angular/router";
import swal from "sweetalert2";
import { Teacher } from "app/shared/models/teacher.model";

@Component({
  selector: "app-edit-teacher",
  templateUrl: "./edit-teacher.component.html",
  styleUrls: ["./edit-teacher.component.scss"]
})
export class EditTeacherComponent implements OnInit {
  updatedTeacher;
  teacherid;

  constructor(
    public teacherservice: TeacherService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedTeacher = new Teacher({});
    this.teacherid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.teacherservice.getTeacherById(this.teacherid).subscribe(
      (data: any) => {
        console.log("data", data);
        this.updatedTeacher = data;
      },
      error => {
        console.log("error-->", error);
      }
    );
  }

  edit() {
    swal
      .fire({
        title: "Do you want to update the Teacher?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Update"
      })
      .then(result => {
        if (result.value) {
          this.teacherservice
            .editTeacher(this.updatedTeacher, this.teacherid)
            .subscribe(
              (data: any) => {
                console.log("teacher added successfully");
                swal
                  .fire("Updated!", "Teacher Updated.", "success")
                  .then(() => {
                    this.router.navigate(["teacher"]);
                  });
                this.ngOnInit();
              },
              error => {
                swal.fire("Not Updated!", "There was some error.", "error");
                console.log("Error", error);
              }
            );
        }
      });

    console.log("Submitted>>>>>>>>>", this.updatedTeacher);
  }
}
