import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { TeacherService } from "./services/teacher.service";
import swal from "sweetalert2";

@Component({
  selector: "app-teacher",
  templateUrl: "./teacher.component.html",
  styleUrls: ["./teacher.component.scss"]
})
export class TeacherComponent implements OnInit {
  teacherList;

  constructor(public teacherservice: TeacherService, private router: Router) {
    this.teacherList = [];
  }

  ngOnInit() {
    this.teacherservice.getTeacher().subscribe(
      (data: any) => {
        this.teacherList = data;
        console.log(data);
      },
      error => {
        console.log(error);
      }
    );
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the Applicant?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          this.teacherservice.deleteTeacher(id).subscribe(
            (data: any) => {
              console.log("Deleted successfully");
              swal.fire("Deleted!", "Applicant Deleted.", "success");
              this.router.navigate(["/teacher"]);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Deleted!", "There was some error.", "error");
              console.log("Error", error);
            }
          );
        }
      });
  }
}
