import { Component, OnInit } from "@angular/core";
import { TeacherService } from "../services/teacher.service";
import { Router } from "@angular/router";
import { Teacher } from "../../shared/models/teacher.model";

import swal from "sweetalert2";

@Component({
  selector: "app-add-teacher",
  templateUrl: "./add-teacher.component.html",
  styleUrls: ["./add-teacher.component.scss"]
})
export class AddTeacherComponent implements OnInit {
  newteacher: Teacher;
  submitting: boolean;

  constructor(public teachersservice: TeacherService, public router: Router) {
    this.newteacher = new Teacher({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newteacher);

    swal
      .fire({
        title: "Do you want to add the teacher ?",
        text: "New teacher will be added in Teacher List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.teachersservice.addTeacher(this.newteacher).subscribe(
            (data: any) => {
              swal.fire("Add!", "Teacher has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
