import { Injectable } from "@angular/core";
import { BaseService } from "../../shared/services/base.service";
import { HttpClient } from "@angular/common/http";
import { Teacher } from "app/shared/models/teacher.model";

@Injectable()
export class TeacherService extends BaseService {
  constructor(public http: HttpClient) {
    super("teacher");
  }

  getTeacher() {
    return this.http.get(this.url);
  }
  addTeacher(data) {
    return this.http.post(this.url + "add/", data, this.getOptions());
  }

  getTeacherById(id) {
    return this.http.get(this.url + "edit/" + id, this.getOptions());
  }
  editTeacher(updatedData: Teacher, id) {
    return this.http.put(
      this.url + "edit/" + id,
      updatedData,
      this.getOptions()
    );
  }
  deleteTeacher(id) {
    return this.http.delete(this.url + "delete/" + id, this.getOptions());
  }
}
